

table{
    margin-left: auto;
    margin-right: auto;
    text-align: center;
    background-color: White;
  }
  tr, td{
    border: 2px solid #fff;
    width: 150px;
    height: 50px; 
    color: black;
    font-weight: bold;
    border-radius: 20px;
  }
  .innerTable
  {
    background-color: #eee;
    color: black;
    font-weight: bold;
    border-radius: 50px;
  }
  
  .table-heading{
    font-size: 30px;
    color: black;
    font-weight: bold;
  }
  
  table{
    overflow-x: auto;
  }
  table tr:nth-child(even) {
   background-color: #eee;
  }
  table tr:nth-child(odd) {
  background-color: #fff;
  }
  #total{
    color: blue;
    text-shadow: 2px 0px 4px blue;
  }
  
  #names{
    text-shadow: 2px 2px 4px black;
  }
  #active{
    color:rgb(255, 211, 0);
    text-shadow: 2px 2px 4px rgb(255, 255, 0);
  }
  
  #deaths{
    color: red;
    text-shadow: 1px 0px 4px red;
  }
  
  #recover{
    color:#00ff00;
    text-shadow: 0px 0px 4px #00ff00;
  }
   
  .state_name:hover{
      cursor: pointer;
  }
  
  .footer{
    position: absolute;
    bottom: 0;  
    width: 100%;
    text-align: center;
    color:white;
    padding: 10px;
  }
  .card
  {
    margin:50px;
    margin-top:30px;
    height:650px;
    background-color: #eee;
    border-radius: 20px;
    padding: 10px;
  }
  .page-container {
    position: relative;
    min-height: 100vh;
  }
  .effect:hover{
    transition: all 1.5s ease;
    transform: scale(1.05);
  }
  .dropdown
  {
    border-radius: 10px;
    margin: 20px;
    height: 40px;
    background-color: #eee;
  }
  .india{
    margin-bottom: 60px;
  }
  .countrySearchTbale
  {
    margin-top:20px;
    margin-bottom:20px;
  }
  .global-table{
    margin-top:5px;
  }
  .stateTable{
    margin-bottom: 15px;
  }
  .singleState{
    margin-bottom:15px;
    margin-top:15px;
  }
  
  .body{
    padding-bottom: 5rem; 
  }
  .loading{
    font-size: 40px;
    text-align: center;
  }
  .main{
    width:90vw;
  }
  .icons{
    height: 10vh;
    width: 20vw;
    box-shadow: 5px 5px 10px 8px rgb(212, 221, 216);
    border-radius: 50px;
  }
  
  .iconm{
    width: 20vw;
    height: 20vh;
    box-shadow: 5px 10px 18px 8px grey;
    border-radius: 50px;
  }
  .iconl{
    height: 25vh;
    width: 20vw;
    box-shadow: 5px 10px 18px 8px grey;
    border-radius: 50px;
  }
    